#settings {
  position: absolute;
  right: 0;
  top: 80px;
  background: #fff0d2;
  color: #a52a2a;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  cursor: pointer;
  border: 2px solid #a52a2a;
  border-right: none;
}

.settings-icon {
  width: 22px;
}

.settings-dialog .dialog--scrollable-content {
  background: transparent;
}

.settings-dialog-content {
  background-color: #fff0d2;
  border-radius: 10px;
  padding: 20px;
}

.settings-dialog-content .dialog-close {
  color: #e47676;
  background: #fff0d2;
  border: 2px solid #e47676;
  right: 30px;
  top: 30px;
}

.settings-dialog-content .dialog-simple-title {
  font-family: ArrrMateyBb;
  text-align: center;
  font-size: 100px;
  color: #a52a2a;
  position: relative;
  top: -35px;
  text-shadow: 3px -4px 0px #fff0d2;
}

.settings-dialog-content .setting-element {
  padding: 10px 0;
}

.settings-dialog-content input[type="checkbox" i]:focus {
  outline-offset: 0;
  outline: none;
}

.settings-dialog-content input[type="checkbox"] {
  appearance: none;
  background: white;
  width: 15px;
  height: 15px;
  border: 2px solid #a52a2a;
  border-radius: 2px;
  margin: 0px 5px -2px 0;
}

.settings-dialog-content label {
  position: relative;
}

.settings-dialog-content .check-icon {
  color: #e47676;
  position: absolute;
  left: -3px;
  top: -9px;
  width: 25px;
  height: 25px;
}

.settings-dialog-content button {
  padding: 4px 8px;
  font-size: 17px;
  font-weight: bold;
  color: #a52a2a;
  background-color: #fff0d2;
  border: 2px solid #a52a2a;
}
.settings-dialog-content button:disabled {
  color: rgba(16, 16, 16, 0.3);
}

.dialog--root.confirm-cancel-game-dialog {
  z-index: 10;
}

.confirm-cancel-game-dialog .dialog--scrollable-content {
  background: transparent;
}

.confirm-cancel-game-dialog .title {
  font-size: 17px;
  color: #591717;
}

.confirm-cancel-game-dialog .title-container {
  margin: 0 10px 30px;
  text-align: center;
}

.confirm-cancel-game-dialog .content {
  color: #591717;
  margin: 10px;
}

.confirm-cancel-game-dialog .illustration {
  margin-bottom: 10px;
  width: 75px;
  height: 75px;
}

.confirm-cancel-game-dialog .settings-dialog-content button {
  margin-top: 30px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 7px 13px;
  font-size: 18px;
  font-weight: bold;
  color: #a52a2a;
  background-color: #fff0d2;
  border: 2px solid #a52a2a;
}
